@mixin reset-btn {
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
}

@mixin hover {
  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      @content;
    }
  }
}
