.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;

  @include vp--1439 {
    padding: 0 20px;
  }

  @include vp--1023 {
    padding: 0 20px;
  }

  @include vp--768 {
    padding: 0 16px;
  }

  &--no-padding {
    @include vp--767 {
      padding: 0;
    }
  }
}
